import React from "react";
import { KeycloakInstance, GraphQLProvider } from "@faharmony/service";
import { HashRouter as Router } from "@faharmony/router";
import { ToastProvider } from "@faharmony/components";
import Div100vh from "react-div-100vh";

/**
 * AppWrapper
 * ---
 * Wraps the App with required context.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 */
const AppWrapper = (props: {
  children: React.ReactNode;
  keycloakInstance: KeycloakInstance;
}) => {
  const { children, keycloakInstance } = props;

  return (
    <Router>
      <ToastProvider>
        <GraphQLProvider keycloakInstance={keycloakInstance}>
          <Div100vh id="full-height-wrapper">{children}</Div100vh>
        </GraphQLProvider>
      </ToastProvider>
    </Router>
  );
};

export default AppWrapper;
