import React, { useContext, useMemo } from "react";
import { useHistory } from "@faharmony/router";
import { TFunction, useTranslation } from "@faharmony/locale";
import { IBreadcrumbsProps } from "@faharmony/components";
import { IModuleProps } from "../types";

const ModuleContext = React.createContext<IModuleProps>({} as IModuleProps);

export const ModuleProvider = (props: {
  Module: IModuleProps;
  children: React.ReactNode;
}) => {
  return (
    <ModuleContext.Provider value={props.Module}>
      {props.children}
    </ModuleContext.Provider>
  );
};

/**
 * Hook to get Module information inside the module.
 */
export const useModule = () => useContext(ModuleContext);

/**
 * Hook to create a breadcrumbs to go back to parent Module.
 */
export const useModuleBreadcrumbs = (): IBreadcrumbsProps["crumbs"] => {
  const { id: ModuleId, label, icon } = useModule();
  const t = useModuleTranslation();
  const history = useHistory();
  return useMemo(
    () => [
      {
        id: ModuleId,
        label: t("moduleName") || label || "",
        icon,
        onClick: () => history.push(`/${ModuleId}`),
      },
    ],
    [history, ModuleId, t]
  );
};

/**
 * Custom hook to get i18n's t-function for current Module
 */
export const useModuleTranslation = (): TFunction =>
  useTranslation(useModule().id).t;

export default useModule;
